import React,{Component} from "react";
import CustomerService from "../Services/CustomerService";
import { Link } from "react-router-dom";

class DisplayCus extends Component
{
    constructor(props)
    {
        super(props)
        this.state={
            customer:[],
            num:1
        }
    }
    componentDidMount()
    {
        CustomerService.fetchCustomer().then(result=>{
            this.setState({
                customer:result.data
            })
        })
    }
    deleteCus(id)
    {
        CustomerService.deleteCustomer(id).then(result=>{
            this.setState({
                customer:this.state.customer.filter(cus=>cus.id!=id)
            })
        })
    }
    updateCus(id)
    {
        this.props.history.push(`/updateCus/${id}`)
    }
   


    render()
    {
        return(
            <div>
                <h1>My Orders</h1>
                <hr/>
                <table class="table table-sm table-dark">
                    <thead>
                        <tr>
                                <th scope="col">#</th>
                                <th scope="col">Product Name</th>
                                <th scope="col">Price</th>
                                <th scope="col">Customer Name</th>
                                <th scope="col">Email</th>
                                <th scope="col">Contact</th>
                                <th scope="col">Address</th>
                                <th scope="col">Payment Mode</th>
                                <th scope="col">Order Date</th>
                                <th scope="col">Delivery Date</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            this.state.customer.map(cus=>
                            <tr>
                            <th scope="row">{this.state.num++}</th>
                            <td>{cus.productname}</td>
                            <td>{cus.price}</td>
                            <td>{cus.name}</td>
                            <td>{cus.email}</td>
                            <td>{cus.contact}</td>
                            <td>{cus.address}</td>
                            <td>{cus.paymentmode}</td>
                            <td>{cus.orderdate}</td>
                            <td>{cus.deliverydate}</td>
                            <td>
                                <button class="btn btn-danger" onClick={()=>this.deleteCus(cus.id)}>Delete</button> &nbsp;
                                <button class="btn btn-warning" onClick={()=>this.updateCus(cus.id)}>Update</button>
                                
                            </td>
                    </tr>

                            )}
                        
                        
                    </tbody>
                    </table>
                    <Link to="/insertCus"><button class="btn btn-success">Add More Items</button></Link> &nbsp;  &nbsp;
                    <Link to="/back"><button class="btn btn-success">Back to Home</button></Link>
           
            </div>
        )
    }
}
export default DisplayCus;