import logo from './logo.svg';
import './App.css';
import{BrowserRouter as Router,Switch,Route} from 'react-router-dom';
import Home from './Components/Home';
import CreateCus from './Components/CreateCus';
import DisplayCus from './Components/DisplayCus';
import UpdateCus from './Components/UpdateCus';




function App() {
  return (
    <Router>
    <div className="App">
    <Switch>
      <Route path="/" exact component={Home}/>
      <Route path="/back" component={Home}/>
      <Route path="/insertCus" component={CreateCus}/>
      <Route path="/fetchCus" component={DisplayCus}/>
      <Route path="/updateCus/:id" component={UpdateCus}/>
    </Switch>
  </div>
  </Router>
    
  );
}

export default App;
