import React,{Component} from "react";
import { Link } from "react-router-dom";



class Home extends Component
{
    render()
    {
        return(
            <div>
                <h1>Welcome To Flipcart</h1>
                <hr/>
                <Link to="/insertCus"><button class="btn btn-success">Place your Order</button></Link> &nbsp;  &nbsp;
                <Link to="/fetchCus"><button class="btn btn-success">My Order</button></Link>
               
                
            </div>
        )
    }
}
export default Home
