import axios from "axios";

class CustomerService
{
    createCustomer(customer)
    {
        return axios.post("http://localhost:9943/cart/cus/saveCus",customer)
    }
    fetchCustomer()
    {
        return axios.get("http://localhost:9943/cart/cus/getCust")
    }
    deleteCustomer(id)
    {
        return axios.delete("http://localhost:9943/cart/cus/deleteCustById/"+id)
    }
    fetchCustomerById(id)
    {
        return axios.get("http://localhost:9943/cart/cus/getCustById/"+id)
    }
    updateCus(customer)
    {
        return axios.put("http://localhost:9943/cart/cus/updateCus/",customer)
    }
}
export default new CustomerService()
